	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Outage at blogg.de","date":"23 Dec 2005","slug":"/2005-12-23-outage-at-bloggde/","tags":"Y2005"},"html":"<p>We've just recovered from a massive network outage at blogg.de (as well as the other sites hosted by nu2m.de). We were down for about 6 hours. All interfaces on our router would freeze immediately upon restart, and it took me a while to figure out the cause (it wasn't the router, so I wasted a couple of hours working on that). Everything now seems to be back up. The hard part will be figuring out why it happened and keeping it from happening again.  </p>\n<p>I'm the sole admin in Hamburg, and it's been a long, lonely evening.</p>"},"next":{"fields":{"title":"Eggnog in Quantity","date":"23 Dec 2005","slug":"/2005-12-23-eggnog-in-quantity/"}},"previous":{"fields":{"title":"Christmas Traditions","date":"25 Dec 2005","slug":"/2005-12-25-christmas-traditions/"}}},{"node":{"fields":{"title":"Eggnog in Quantity","date":"23 Dec 2005","slug":"/2005-12-23-eggnog-in-quantity/","tags":"Y2005"},"html":"<p><a href=\"http://www.salon.com/mwt/time/1997/12/02time2.html\" title=\"Salon | Time for one thing: All eggnogged up and nowhere to go\">Eggnog in Quantity</a>: \"Too much of anything is bad, but too much whiskey is just enough.\"</p>"},"next":{"fields":{"title":"Cheney's iPod","date":"23 Dec 2005","slug":"/2005-12-23-cheneys-ipod/"}},"previous":{"fields":{"title":"Outage at blogg.de","date":"23 Dec 2005","slug":"/2005-12-23-outage-at-bloggde/"}}},{"node":{"fields":{"title":"Cheney's iPod","date":"23 Dec 2005","slug":"/2005-12-23-cheneys-ipod/","tags":"Y2005"},"html":"<p><a href=\"http://www.boingboing.net/2005/12/22/cheneys_ipod_first_i.html\" title=\"Boing Boing: Cheney's iPod: first in line of succession for power outlets.\">Cheney's iPod: first in line of succession for power outlets</a> on board Air Force II, blocking reporters from charging their notebooks. I'm glad I'm not the only one who has problems keeping his iPod charged.</p>"},"next":{"fields":{"title":"Sleep Two Times","date":"22 Dec 2005","slug":"/2005-12-22-sleep-two-times/"}},"previous":{"fields":{"title":"Eggnog in Quantity","date":"23 Dec 2005","slug":"/2005-12-23-eggnog-in-quantity/"}}}],"pathPrefix":"","first":false,"last":false,"index":255,"pageCount":1101}}

